.container {
  padding: 0px;
  margin: 0 auto;
  width: 80%;
}

.container-fluid {
  padding: 0px;
  width: 100%;
}

.container .row,
.container-fluid .row {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-gap: 10px 10px;
  padding: 0px;
}

.container .row [class^=col-],
.container-fluid .row [class^=col-] {
  padding: 10px;
}
.container .row .col-1,
.container-fluid .row .col-1 {
  grid-column: span 1;
}
.container .row .col-2,
.container-fluid .row .col-2 {
  grid-column: span 2;
}
.container .row .col-3,
.container-fluid .row .col-3 {
